



























import Vue from 'vue';

export default Vue.extend({
  name: 'PlayerInfo',
  computed: {
    playerName(): string {
      const sid = this.$store.state.session.currentSessionId;
      if (sid && this.$store.state.user.users[sid]) {
        return this.$store.state.user.users[sid].sName;
      }
      return '';
    },
  },
});
